/* eslint-disable jsx-a11y/media-has-caption */
import { useTranslation } from 'react-i18next';
import React from 'react';

// Assets
import { ReactComponent as PauseSVG } from 'assets/icons/pause.svg';
import { ReactComponent as PlaySVG } from 'assets/icons/play.svg';

// Styles
import { StyledPlayerPlayButton, StyledCoughExampleContainer, StyledPlayerPlayContainer } from './style';

interface CoughExampleProps {
  audioUrl: string;
}

const CoughExample = ({
  audioUrl,
}: CoughExampleProps) => {
  // Refs
  const refAudio = React.useRef<HTMLMediaElement>(null);

  // Hooks
  const { t } = useTranslation();

  // States
  const [playing, setPlaying] = React.useState(false);

  // Callbacs
  const handlePlay = React.useCallback(() => {
    if (!playing) {
      if (refAudio.current) {
        refAudio.current.play();
      }
    }
  }, [playing]);

  const handlePause = React.useCallback(() => {
    if (playing) {
      if (refAudio.current) {
        refAudio.current.pause();
      }
    }
  }, [playing]);

  // Effects
  React.useEffect(() => {
    const fnPlaying = () => {
      setTimeout(() => {
        setPlaying(true);
      }, 0);
    };

    const fnPause = () => {
      setPlaying(false);
    };

    if (refAudio.current) {
      refAudio.current.addEventListener('playing', fnPlaying);
      refAudio.current.addEventListener('pause', fnPause);
    }

    return () => {
      if (refAudio.current) {
        refAudio.current.removeEventListener('playing', fnPlaying);
        // eslint-disable-next-line react-hooks/exhaustive-deps
        refAudio.current.removeEventListener('pause', fnPause);
      }
    };
  }, []);

  return (
    <StyledCoughExampleContainer>
      {
        audioUrl && (
          <audio ref={refAudio}>
            <source src={audioUrl} />
          </audio>
        )
      }
      <StyledPlayerPlayContainer
        onClick={playing ? handlePause : handlePlay}
      >
        <StyledPlayerPlayButton>
          {playing ? <PauseSVG /> : <PlaySVG />}
        </StyledPlayerPlayButton>
      </StyledPlayerPlayContainer>
      {playing ? t('recordingsRecord:pause') : t('recordingsRecord:play')}
    </StyledCoughExampleContainer>
  );
};

export default CoughExample;
